html, body {
  height: 100%;
}
h1, h2, h3, h4, h5 {
  /* Font */
  font-family: 'Raleway', sans-serif;
  font-weight: 300;
}
a, a:link, a:visited, a:hover {
  /* Transition */
  -webkit-transition: 0.2s ease-in-out;
  transition: 0.2s ease-in-out;
}
.wrapper, #app {
  height: 100%;
}
.card {
  /* Margin & Padding */
  margin-bottom: 20px;
}
/*
 * Main Navigation
 */
.wrapper .main-nav {
  background-color: #3c3b3d;
  overflow-y: auto;
  /* Font */
  color: #ffffff;
}
.main-nav .logo {
  background-color: #323133;
  overflow: auto;
  /* Font */
  font-family: 'Raleway', sans-serif;
  font-weight: 300;
  text-align: center;
  /* Margin & Padding */
  padding: 10px;
}
.main-nav .logo a, .main-nav .logo a:link, .main-nav .logo a:visited, .main-nav .logo a:hover {
  /* Font */
  color: #ffffff;
}
.main-nav .vert-nav {
  /* Margin & Padding */
  padding: 0;
}
.main-nav .vert-nav .heading {
  background-color: #323133;
  /* Font */
  font-family: 'Raleway', sans-serif;
  font-weight: 300;
  /* Margin & Padding */
  padding: 5px 10px 5px 10px;
  /* Border */
  border-bottom: 1px solid #434a54;
}
.profile-container {
  /* Position */
  position: relative;
}
.main-nav .vert-nav .profile-heading {
  background-color: #6a50a7;
  background: #6a50a7;
  width: 100%;
  /* Font */
  font-family: 'Raleway', sans-serif;
  font-weight: 300;
  text-align: left;
  /* Position */
  position: relative;
  /* Margin & Padding */
  padding: 10px;
  /* Border */
  border: none;
  /* Border Radius */
  border-radius: 0;
}
.main-nav .vert-nav .profile-heading:after {
  content: '\F078';
  /* Font */
  font-family: 'Font Awesome 5 Free';
  font-weight: 900;
  /* Position */
  position: absolute;
  right: 10px;
  top: 10px;
}
.vert-nav ul {
  list-style: none;
  /* Margin & Padding */
  margin: 0;
  padding: 0;
}
.vert-nav ul > li {
  /* Border */
  border-bottom: 1px solid #434a54;
  /*  Margin & Padding */
  margin: 0;
  padding: 0;
}
.vert-nav ul > li > a, .vert-nav ul > li > a:link, .vert-nav ul > li > a:visited {
  display: block;
  /* Position */
  position: relative;
  /* Font */
  color: #ffffff;
  /* Margin & Padding */
  padding: 10px;
}
.vert-nav ul > li > a:hover {
  background-color: #434a54;
  /* Font */
  text-decoration: none;
}
.vert-nav ul > li > a:after {
  content: '\F105';
  /* Font */
  font-family: 'Font Awesome 5 Free';
  font-weight: 900;
  /* Position */
  position: absolute;
  right: 10px;
  top: 10px;
}
.vert-nav ul > li > a.current, .vert-nav ul > li > a:link.current, .vert-nav ul > li > a:visited.current, .vert-nav ul > li > a:hover.current {
  background-color: #ffffff;
  /* Font */
  color: #323133;
}
@media screen and (max-width: 767px) {
.vert-nav {
    display: none;
}
}
@media screen and (min-width: 786px) {
.vert-nav {
    display: block !important;
}
}
/*
 * Main Content
 */
.wrapper .main-content {
  height: 100%;
  overflow-y: auto;
  /* Margin & Padding */
  padding-top: 20px;
  padding-bottom: 20px;
}
/*
 * SimpleMDE
 */
.markdown-editor {
  /* Font */
  font-family: 'Monaco', courier, monospace;
}
.editor-preview, .editor-preview h1, .editor-preview h2, .editor-preview h3, .editor-preview h4, .editor-preview h5 {
  /* Font */
  font-family: 'Merriweather', serif;
}
/*
 * Page Editor
 */
.page-toolbar {
  /* Margin & Padding */
  margin-bottom: 20px;
}
.movable {
  /* Cursor */
  cursor: move;
}
/*
 * Footer
 */
.footer a, .footer a:link, .footer a:visited {
  /* Font */
  color: #808080;
}
.footer a:hover {
  /* Font */
  text-decoration: none;
  /* Border */
  border-bottom: 1px solid #808080;
}
/*
 * Scrollbar
 */
::-webkit-scrollbar-track {
  border-radius: 2px;
  background-color: transparent;
  /* $gray-200 */
}
::-webkit-scrollbar {
  width: 5px;
}
::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: rgba(101, 109, 120, 0.3);
}
